import {useState} from 'react'
import { CRow, CCol, CFormSelect } from '@coreui/react'
import CIcon from '@coreui/icons-react'
import { cilPenAlt } from '@coreui/icons'
import { useOutletContext } from 'react-router-dom'
import { ModalSezioneTesta } from '../../../Forms/puntoLuce/modal_sezione_testa'
import { ModalSezioneSecondoInnesto } from '../../../Forms/puntoLuce/modal_secondo_innesto'
import { ModalSezionePrimoInnesto } from '../../../Forms/puntoLuce/modal_primo_innesto'
import { ModalSezioneBase } from '../../../Forms/puntoLuce/modal_sezione_base'
import BASE_URL from '../../../config/baseURL'


export const VerStrutturale = () => {
  const [plData, setPlData] = useOutletContext()
  const [isVisibleSezioneTesta, setIsVisibleSezioneTesta] = useState(false)
  const [isVisibleSezioneSecondoInnesto, setIsVisibleSezioneSecondoInnesto] = useState(false)
  const [isVisibleSezionePrimoInnesto, setIsVisibleSezionePrimoInnesto] = useState(false)
  const [isVisibleSezioneBase, setIsVisibleSezioneBase] = useState(false)

  return (
    <>
    <ModalSezioneBase title="Modifica Dati Sezione Secondo Innesto" isVisible={isVisibleSezioneBase} data={plData.verifica_strutturale.sezione_base} setPlData={setPlData} plData={plData} onClose={() => setIsVisibleSezioneBase(false)} />
    <ModalSezionePrimoInnesto title="Modifica Dati Sezione Secondo Innesto" isVisible={isVisibleSezionePrimoInnesto} data={plData.verifica_strutturale.sezione_primo_innesto} setPlData={setPlData} plData={plData} onClose={() => setIsVisibleSezionePrimoInnesto(false)} />
    <ModalSezioneSecondoInnesto title="Modifica Dati Sezione Secondo Innesto" isVisible={isVisibleSezioneSecondoInnesto} data={plData.verifica_strutturale.sezione_secondo_innesto} setPlData={setPlData} plData={plData} onClose={() => setIsVisibleSezioneSecondoInnesto(false)} />
    <ModalSezioneTesta title="Modifica Dati Sezione di Testa" isVisible={isVisibleSezioneTesta} data={plData.verifica_strutturale.sezione_testa} setPlData={setPlData} plData={plData} onClose={() => setIsVisibleSezioneTesta(false)}/>
      <CRow>
        <CCol>
            <div className='size-text color-base weight-600 mb-2'>Scegli Modello</div>
            <CFormSelect
            className='size-text color-base weight-400 rounded-1 py-1 border-light w-75'
            onChange={e => setPlData({...plData, verifica_strutturale : {...plData.verifica_strutturale, modello: e.target.value}})}
            >
              <option>Seleziona modello</option>
              <option className='size-text color-base'>Torri 2 Tronchi</option>
            </CFormSelect>
            <div className='size-text color-base weight-700 my-2 cursor-pointer' onClick={() => setIsVisibleSezioneTesta(true)}>SEZIONE DI TESTA <CIcon size='sm' icon={cilPenAlt}/></div>
            <div className='size-text color-base weight-700 mb-2 cursor-pointer' onClick={() => setIsVisibleSezioneSecondoInnesto(true)}>SEZIONE SECONDO INNESTO <CIcon size='sm' icon={cilPenAlt}/></div>
            <div className='size-text color-base weight-700 mb-2 cursor-pointer' onClick={() => setIsVisibleSezionePrimoInnesto(true)}>SEZIONE PRIMO INNESTO <CIcon size='sm' icon={cilPenAlt}/></div>
            <div className='size-text color-base weight-700 mb-2 cursor-pointer' onClick={() => setIsVisibleSezioneBase(true)}>SEZIONE BASE <CIcon size='sm' icon={cilPenAlt}/></div>
        </CCol>
        <CCol>
            <div className='size-text color-base weight-600 mb-2'>Anteprima Disegno Grafico</div>
            <img onError={(e) => e.target.style.display = "none"} alt='img_verifica_strutturale' src={BASE_URL+plData.dati_generali.verifica_strutturale_file}/>
        </CCol>
      </CRow>
    </>
  )
}
