import React, { useEffect } from 'react'
import { useState } from 'react'
import CIcon from '@coreui/icons-react'
import { cilVerticalAlignCenter } from '@coreui/icons'
import { CHeaderBrand, CContainer, CCol, CForm, CCard, CCardBody, CNavbar, CButton } from '@coreui/react'
import { useParams, NavLink, Outlet } from 'react-router-dom'
import { axiosPrivate } from '../../../api/axios'
export const TorreFaro = (props) => {
    const { pl_id, commessa_cod } = useParams()
    const [isLoading, setIsLoading] = useState(false)
    // const [refresh, setRefresh] = useState(false)
    const menuItems = [
        {
        text: "Dati Generali",
        to: ''
      },
        {
        text: "Visual Test",
        to: 'visual_testing'
      },
        {
        text: "Ultrasonic Test",
        to: 'ultrasonic_testing'
      },
        {
        text: "Magnetic Test",
        to: 'magnetic_testing'
      },
        {
        text: "Ver. Strutturale",
        to: 'ver_strutturale'
      },
        {
        text: "Ver. Elettromeccanica",
        to: 'ver_elettromeccanica'
      },
        {
        text: "Note",
        to: 'note'
      },
        {
        text: "Ver. Rettilineit",
        to: 'ver_rettilineita'
      },
        {
        text: "Attestato",
        to: 'attestato'
      }
      ]
    const [plData, setPlData] = useState({
      pl_id: pl_id,
      dati_generali: {
        committente: "",
        costruttore: "",
        data: "",
        installazione_cantiere: "",
        commessa_cod: commessa_cod,
        tipoligia: "",
        n_torre: "",
        tipoligia_tirafondi: "",
        dist_asse_tirafondi: "",
        n_tirafondi: "",
        diametro_base: "",
        altezza: "",
        materiale: "",
        cond_superficiali: "",
        indirizzo: "",
        planimetria_torre_file: "",
        verifica_strutturale_file: ""
      },
      visual_testing: {
        esame_diretto: false,
        esame_indiretto: false,
        esame_diretto_tipi: [],
        esame_indiretto_tipi: [],
        dist_osservazione: "",
        attrezz_ausiliarie: "",
        angolo_osservazione: "",
        illuminazione: "",
      },
      ultrasonic_testing: {
        spessimetro_marca_modello: "",
        spessimetro_attrezz_ausiliarie: "",
        trasduttore_1_f_d_a: "",
        trasduttore_1_marca_tipo: "",
        trasduttore_2_f_d_a: "",
        trasduttore_2_marca_tipo: "",
        rivelatore_universale_marca_tipo: "",
        rivelatore_universale_attrezz_ausiliarie: "",
      },
      magnetic_testing: {
        marca_modello: "",
        tipologia_corrente: "",
        attrezz_ausiliarie: "",
        f_d_a: "",
      },
      verifica_strutturale: {
        modello: "",
        sezione_testa: {
          n: "3,5",
          e: "",
          s: "",
          w: "",
          vt: "",
          ut: "",
          mt: "",
          crf: "",
          note: "",
          esito: ""
        },
        sezione_primo_innesto: {
            tr2_n: "", tr2_e: "", tr2_s: "", tr2_w: "" ,
            tr3_n: "", tr3_e: "", tr3_s: "", tr3_w: "" ,
            vt: "",
            ut: "",
            mt: "",
            crf_tr2: "",
            crf_tr3: "",
            note: "",
            esito: ""
        },
        sezione_secondo_innesto: {
            tr1_n: "", tr1_e: "", tr1_s: "", tr1_w: "" ,
            tr2_n: "", tr2_e: "", tr2_s: "", tr2_w: "" ,
            vt: "",
            ut: "",
            mt: "",
            crf_tr1: "",
            crf_tr2: "",
            note: "",
            esito: ""
        },
        sezione_base: {
            n: "3,5",
            e: "",
            s: "",
            w: "",
            vt: "",
            ut: "",
            mt: "",
            crf: "",
            note: "",
            esito: ""
        },
        img: "",
      },
      voci: [],
      note: [],
      verifica_rettilineita: {
        diametro: {
          tirafondo_1: "",
          tirafondo_2: "",
          n: "",
        },
        zona_misura: {
          0: "",
          90: "",
          180: "",
          360: "",
        },
      },
      attestato: {
        torre: "",
        luogo: "",
        comune: "",
        tecnico_1: "",
        tecnico_2: "",
        misurazioni: `i quali hanno eseguito le seguenti analisi e misurazioni:
        verifica visiva sulla superficie esterna del sostegno alla base dincastro e nella parte in elevazione;
        verifica della geometria del sostegno;
        annotazione di tutte le anomalie riscontrate;
        misura UTS dello spessore max e min del palo in pi punti se necessario;
        caratterizzazione morfologica delle eventuali forme di corrosione presenti sulla superficie esterna del sostegno nella zona dincastro con il terreno (per terreno si deve intendere il materiale nel quale il palo  allocato);
        misura della velocit di corrosione CRT per la zona del palo inserita nel terreno compatibilmente con la messa a terra del palo e lomogeneit del terreno e in funzione della reale necessit di rilevare tale misura;
        realizzazione di una o pi foto in funzione di quanto rilevato;
        utilizzando la strumentazione di seguito riportata idonea alla tipologia di verifica in oggetto, regolarmente omologata e sottoposta a periodiche revisioni di taratura annuale:`
      }
    });

    const handleSavePl = async () =>{
      setIsLoading(true)
      try{
        await axiosPrivate.post("pl/updateTorreFaro", plData, { headers: {'Content-Type' : 'multipart/form-data'}})
        setIsLoading(false)
      }
      catch (err){
        setIsLoading(false)
      }
    }

    useEffect(() =>{
      const getTorreFaro = async () =>{
        setIsLoading(true)
        try{
          const res = await axiosPrivate.get("/pl/getTorreFaro/"+pl_id)
          setPlData({...plData, 
            dati_generali: {...JSON.parse(res.data[0]?.dati_generali), planimetria_torre_file: res.data[0]?.planimetria_torre_file, verifica_strutturale_file: res.data[0]?.verifica_strutturale_file} || plData.dati_generali,
            visual_testing: JSON.parse(res.data[0]?.visual_testing) || plData.visual_testing,
            ultrasonic_testing: JSON.parse(res.data[0]?.ultrasonic_testing) || plData.ultrasonic_testing,
            magnetic_testing: JSON.parse(res.data[0]?.magnetic_testing) || plData.magnetic_testing,
            verifica_strutturale: JSON.parse(res.data[0]?.verifica_strutturale) || plData.verifica_strutturale,
            voci: JSON.parse(res.data[0]?.voci) || [],
            note: JSON.parse(res.data[0]?.voci) || [],
            verifica_rettilineita: JSON.parse(res.data[0]?.verifica_rettilineita) || plData.verifica_rettilineita,
            attestato: JSON.parse(res.data[0]?.attestato) || plData.attestato
          })
          }
          catch (err){
          }
        }
        getTorreFaro()
        setIsLoading(false)
  }, [])

    return (
    <>
    <CContainer className="my-3 contrast justify-content-center rounded-1">
        <CNavbar className="contrast rounded-1 w-100 d-flex justify-content-start rounded-1 m-0 py-3" expand='lg' colorScheme='light'>
          {menuItems.map((item, index) => 
          (
              <NavLink key={index} className={({isActive, isPending}) => `${(isActive)? "color-highlight" : "color-base"} size-text weight-600 d-flex align-items-center nav-link`} to={item.to} end>
                  <CIcon icon={item.icon} />&nbsp; {item.text}
              </NavLink>
          ))}
        </CNavbar>
    </CContainer>
    <CContainer className="my-3 p-4 contrast justify-content-center rounded-1">
        <CCol className='d-flex justify-content-between mb-3'>
            <CHeaderBrand className='size-header weight-600 d-flex align-items-center'><CIcon size='lg' className='color-highlight' icon={cilVerticalAlignCenter}/>&nbsp;{commessa_cod}</CHeaderBrand>
            <CButton onClick={handleSavePl} shape="color-base rounded-1 w-25 contrast border-0 size-text d-flex justify-content-center shadow-sm weight-600">Salva punto luce</CButton> 
        </CCol>
        <CCard className='background-light border-0 shadow-sm'>
            <CCardBody>
            <CForm encType="multipart/form-data" className="justify-content-center rounded-1 px-3 py-2">
              <Outlet context={[plData, setPlData]}/>
            </CForm>
            </CCardBody>
        </CCard>
    </CContainer>
    </>
    )
}
