import { axiosPrivate } from "./axios"
export const getAreeTerritoriali = async (setFunction) =>{
    try{
        const res = await axiosPrivate.get("areeTerritoriali")
        setFunction(res.data)
    }
    catch (err){
        console.log(err)
    }
  }
